body {
	background-color: #F5F5F5;
}
.btn {
    display: inline-block;
    padding: 6px 12px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42857143;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid #2e6da4;
    border-radius: 4px;
    color: #fff;
    background-color: #337ab7;
    margin-left: 10px;
    position: fixed;
    right: 20px;
    top: calc(50% - 5px);
    z-index: 100;
    opacity: .4;
}
.btn.btn2 {
    top: calc(50% + 34px);
}
.app .jsoneditor-poweredBy { contenteditable: true }
.app .contents {
  width: 100%;
  height: calc(100vh - 9.9vh);
  margin: 40px auto 0;
  outline: 1px solid #f0f0f0
}

.app .contents .menu {
  padding: 10px 0;
}

.app .contents .code {
  background: #f5f5f5;
}

.app .jsoneditor {
	width: 600px;
	border-color: transparent;
    margin-right: 16px;
}
.app .jsoneditor-menu {
    background-color: #7fa360;
    border-bottom: 1px solid #7fa360;
}
